<template>

    <div class="parent position-relative p-0 m-0 border border-0">

        <LoaderComponent :show="showLoader" :percentLoaded="percentLoaded"></LoaderComponent>

        <OrbitalMenu @onPlay="play" @onStep="step" @onSetLevel="setLevel" @onShowFilter="showFilter"></OrbitalMenu>

        <div class="orbital-canvas" id="orbital">
            <PopoverComponent></PopoverComponent>
        </div>

        <FilterComponent :min="49" :max="66" :step="0.1" :gap="0" :showToast="showToast" @onCloseToast="closeFilter"
            @onSurfaceChanged="surfaceChanged">
        </FilterComponent>

    </div>

</template>



<script>

import { Orbital } from "@/canvas-modules/orbital";
import PopoverComponent from "./PopoverComponent.vue";
import FilterComponent from "./FilterComponent.vue";
import LoaderComponent from "./LoaderComponent.vue";
import OrbitalMenu from "./OrbitalMenu.vue";


export default {
    name: 'CanvasWorld',

    components: {
        PopoverComponent,
        FilterComponent,
        LoaderComponent,
        OrbitalMenu
    },

    props: {
        msg: String
    },

    data() {
        return {
            orbital: null,

            showToast: false,

            showLoader: true,

            percentLoaded: 0
        }
    },

    methods: {
        init() {
            this.orbital = new Orbital("orbital");
            this.orbital.init();

            window.addEventListener("OnLoadingChange", () => {
                this.percentLoaded = this.orbital.getPercentLoaded();
            });

            window.addEventListener("OnAppReady", () => {
                this.showLoader = false;
            });


            window.addEventListener("OnHSClick", (e) => {

                const detail = e.detail;
                const region = detail.region;
                if (region === undefined) return;

                alert(region.name);

            });
        },

        setLevel(level) {
            this.orbital.setLevel(level);
        },

        play(play) {
            if (play) this.orbital.playOrbital();
            else this.orbital.stopOrbital();
        },

        step(reverse) {
            if (reverse)
                this.orbital.prev();
            else
                this.orbital.next()
        },

        showFilter() {
            this.showToast = true;
        },
        closeFilter() {
            this.showToast = false;
        },

        surfaceChanged(surface) {
            this.orbital.filterHSByPrice(surface);
        }
    },

    mounted() {
        this.init();
    }
}
</script>


<style>
.orbital-canvas {
    position: relative;
    padding: 0 !important;
    margin: 0 !important;
    border: none !important;
}


.parent {
    height: 100vh;
}
</style>
